import {
  // postBoard, commit      vue    
  // patchBoard,
  // deleteBoard,
  detailBoard,
  listBoard,
} from "@/api/board.js";

const boardStore = {
  namespaced: true,
  state: {
    watchingBoard: [],
    noticeList: [],
    questionList: [],
  },
  getters: {},
  mutations: {
    SET_DETAILBOARD: (state, board) => {
      state.watchingBoard = board;
    },
    SET_NOTICEBOARDLIST: (state, boards) => {
      state.noticeList = boards;
    },
    SET_QUESTIONBOARDLIST: (state, boards) => {
      state.questionList = boards;
    },
    SET_STATE: (state) => {
      const initialState = {
        watchingBoard: [],
        noticeList: [],
        questionList: [],
      };
      Object.assign(state, initialState);
    },
  },
  actions: {
    //  
    async detailBoardStore({ commit }, boardSeq) {
      await detailBoard(
        boardSeq,
        (response) => {
          console.log(response);
          commit("SET_DETAILBOARD", response); // response   
        },
        (error) => {
          console.log(error);
        }
      );
    },

    //   
    async listBoardStore({ commit }, boardCode) {
      await listBoard(
        boardCode,
        (response) => {
          console.log(response.data);
          if (boardCode === 201) {
            commit("SET_NOTICEBOARDLIST", response.data);
          } else if (boardCode === 202) {
            commit("SET_QUESTIONBOARDLIST", response.data);
          }
        },
        (error) => {
          console.log(error);
        }
      );
    },
    //  
    boardStoreReset({ commit }) {
      commit("SET_STATE");
    },
  },
};

export default boardStore;
