<template>
  <!--  -->
  <div v-show="!this.back" class="searchPostcard front" >
    <div>
      <img :src="searchItem.postcard.postcardImgUrl" class="searchPostcardImg">
    </div>
    <button class="searchPostcardButton" @click="changeFace">
      
    </button>
    <i v-show="!isLiked" @click="dolikeLetter(searchItem.postcard.postcardSeq)"  class="fa-solid fa-heart searchPostcardLike" ></i>
    <i v-show="isLiked" @click="dodislikeLetter(searchItem.postcard.postcardSeq)" class="fa-regular fa-heart searchPostcardLike"></i>
  </div>
  <!--  -->
  <div v-show="this.back" class="searchPostcard back">
    <!--  -->
    <div class="searchMade">
      <h5 style="margin-bottom:0;">: </h5>
      <button class="searchProfileBtn" @click="goArtistMypage(this.searchItem.postcard.userSeq)">
        {{this.searchItem.userId}}
      </button>
    </div>
    <hr>
    <div class="searchTag">
      <div v-for="(tag, idx) in this.searchItem.tag" :key="`postcard-${idx}`">
        <button @click="submitSearch(tag)" class="searchProfileTag">{{tag}}</button>
      </div>
    </div>
    <button class="searchPostcardButton" @click="changeFace">
      
    </button>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import { likeLetter, dislikeLetter } from "@/api/mainpage.js";
const mainpageStore = "mainpageStore";
const accountStore = "accountStore";
const postcardStore = "postcardStore";
const searchStore = "searchStore"

export default {
  name: "SearchPostCard",
  props: {
    searchItem: {
      type: Object,
    }
  },
  computed: {
    ...mapState(accountStore, ["userInfo"]),
    ...mapState(postcardStore, ["likedPostcards"]), //    
  },
  data() {
    return {
      isLiked: false,
      back: false,
    }
  },
  methods: {
    ...mapActions(mainpageStore, ["likeLetterStore", "dislikeLetterStore"]),
    ...mapActions(postcardStore, ["userLikedPostcardStore"]),
    ...mapActions(searchStore, ["getSearchResult"]),
    // 
    async dolikeLetter(postcardSeq) {
      await likeLetter(postcardSeq, this.userInfo.userSeq);
      await this.userLikedPostcardStore(this.userInfo.userSeq); //       
      this.isLiked = !this.isLiked;
    },
    // 
    async dodislikeLetter(postcardSeq) {
      await dislikeLetter(postcardSeq, this.userInfo.userSeq);
      await this.userLikedPostcardStore(this.userInfo.userSeq); //        
      this.isLiked = !this.isLiked;
    },
    // 
    async submitSearch(tag) {
      await this.getSearchResult(tag);
      this.$router.push('/search');
    },
    //   
    changeFace() {
      this.back = !this.back
    },

    //    
    goArtistMypage(userSeq) {
      this.$router.push({path: `/mypage/${userSeq}`, query: {ownerSeq: userSeq}});
    },

  },
  async created() {
    await this.likedPostcards.forEach((postcard) => {
        if (postcard.postcard.postcardSeq === this.searchItem.postcard.postcardSeq) {
          this.isLiked = true;
          return false;
        }  
      });
    },
    //    ,      ?
    //      
    //    
    //      
    //    isLiked   
}
</script>

<style>
/*    */
.searchPostcard {
  position: relative;
  width: 18vw;
  height: 18vw;
  background-color: antiquewhite;
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 2vh;
  /*   */
  filter: drop-shadow(10px 6px 6px #c3c3c3);
  /*   -   */
  backface-visibility: hidden;
  transition: 0.7s;
}
.searchPostcard:hover {
  transform: scale3d(1.3, 1.3, 1.3);
}
.searchPostcard .front {
  /*    ,      ->    */
  position: absolute;
  /*   ,  */
  transform: rotateY(0deg);
}
.searchPostcard .back {
  /*    ,      ->    */
  position: absolute;
  /*   ,  */
  transform: rotateY(0deg);
}
.searchPostcard:hover .front {
  transform: rotateY(180deg);
}
.searchPostcard .back {
  transform: rotateY(-180deg);
}
.searchPostcard .back {
  transform: rotateY(0deg);
}

/*   */
.searchPostcardImg {
  width: 18vw;
  height: 15.5vw;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
/*   */
.searchPostcardButton {
  cursor: pointer;
  position: absolute;
  right: 0.5vw;
  bottom: 0.3vw;
  border-radius: 5px;
  border: 0px;
  overflow: auto;
  background-color: azure;
}
/*   */
.searchPostcardLike {
  position: absolute;
  cursor: pointer;
  top: 16.3vw;
  left: 5%;
  font-size: 1.5vw;
  border-radius: 20px;
  color: red;
}
/*  */
/*  */
.searchMade {
  margin-left: 1vw;
  margin-top: 1vw;
  display: flex;
}
/*    */
.searchProfileBtn {
  cursor: pointer;
  border-radius: 5px;
  border: 0px;
  overflow: auto;
  background-color: azure;
}
.searchProfileBtn:hover {
  transform: scale3d(1.3, 1.3, 1.3);
}

/*  */
.searchTag {
  background-color: beige;
  width: 16vw;
  height: 10vw;
  margin: auto;
  display: flex;
  justify-content: flex-start;
  border: 0.5px grey dashed;
  border-radius: 5px;
  overflow: hidden;
}
/*   */
.searchProfileTag {
  width: auto;
  height: 30px;
  margin: 0.5vw;
  cursor: pointer;
  border-radius: 5px;
  border: 0px;
  background-color: azure;
}
.searchProfileTag:hover {
  transform: scale3d(1.3, 1.3, 1.3);
}
</style>