        <div class="canvasTagCheck">
          <div class="canvasTagItem" v-for="(ta, index) in tag" :key="index" @click="deleteTag(index)">
            #{{ ta }},&nbsp;
          </div>
        </div>
      </div>

      <div class="tagRemoveInfo">    !</div>
      <div class="resultSaveBtn" @click="upload"> </div>
      <div class="resultTitleBend" @click="closeModal2"> </div>
    </div>
