<script>
import SideBar from "@/components/Nav/SideBar.vue";
import html2canvas from 'html2canvas';
import { mapState, mapActions, mapGetters } from "vuex";
const accountStore = "accountStore";
const postcardStore = "postcardStore";


export default {
  name: "HtmlCanvasView",
  components: {
    SideBar,
  },
  data() {
    return {
      x: 0,
      y: 0,
      painting: false,
      ctx: "",
      color: "#a8c832",
      lineFat: 20,
      opened1: false,
      opened2: false,
      toWhatPic: "",
      tag: [],
      tagItem: "",
      resultimg: "",
      showFin: false,
      colorMap: {
        "#5e5bc5": "",
        "#706419": "",
        "#7f4502": "",
        "#8f2a91": "",
        "#9600b1": "",
        "#aad16a": "",
        "#ae2974": "",
        "#b0c1c3": "",
        "#6e6e28": "",
        "#7c32c8": "",
        "#7d3054": "",
        "#87716f": "",
        "#8b3027": "",
        "#946e28": "",
        "#999900": "",
        "#696969": "",
        "#77ba1d": "",
        "#7ec864": "",
        "#869664": "",
        "#9364c8": "",
        "#956432": "",
        "#9ac6da": "",
        "#9ceedd": "",
        "#9e9eaa": "",
        "#a1a164": "",
        "#b1c8ff": "",
        "#606e32": "",
        "#760000": "",
        "#7bc800": "",
        "#a2a3eb": "",
        "#a8c832": "",
        "#b57b00": "",
      },
    }
  },
  computed: {
    ...mapState(postcardStore, ["justUploadedPostcard"]),
    ...mapGetters(accountStore, ["userInfo"]),
  },
  methods: {
    ...mapActions(postcardStore, ["uploadPostcard", "uploadTag"]),
    // 1 /
    openModal1() {
      this.opened1 = true;
    },
    closeModal1() {
      this.opened1 = false;
    },

    // 2 /
    openModal2() {
      this.opened2 = true;
    },
    closeModal2() {
      this.opened2 = false;
    },

    //  
    appendTag() {
      // this.tag.push(this.tagItem);
      console.log(this.tagItem);
      if (!this.tag.includes(this.tagItem)) {
        this.tag.unshift(this.tagItem);
        this.tagItem = "";
      }
      this.tagItem = "";
      console.log(this.tag);
    },

    //  
    deleteTag(idx) {
      this.tag.splice(idx, 1);
    },

    //     
    async changeImage() {
      await console.log("hi");
      //   formdata    +     
      // await ,  formdata       
      // :    ->      ->    ->    
      await this.closeModal1();
      await this.openModal2();
    },

    //  
    async upload() {
      const element = document.getElementById("uploadFile");
      const canvas = await html2canvas(element);
      const dataUrl = canvas.toDataURL("image/png");
      const blobData = this.dataURItoBlob(dataUrl);
      // 
      const now = new Date();
      //  
      const filename = `yeupseo-${this.userInfo.userSeq}${now.getHours()}${now.getMinutes()}${now.getSeconds()}.png`
      //  
      const tempFile = new File([blobData], filename, { type: 'image/png' });
      // 
      let canvasData = new FormData;
      canvasData.append('postcard', tempFile);

      let postcardObj = {
        userId: this.userInfo.userId,
        postcard: canvasData,
      }
      await this.uploadPostcard(postcardObj);
      let tagObj = {
        postcardSeq: this.justUploadedPostcard.postcardSeq,
        tagList: this.tag,
      }

      await this.uploadTag(tagObj);
      await this.showFinModal();
    },

    showFinModal() {
      this.showFin = !this.showFin;
    },

    dataURItoBlob(dataURI) {
      var binary = atob(dataURI.split(',')[1]);
      var array = [];
      for (var i = 0; i < binary.length; i++) {
        array.push(binary.charCodeAt(i));
      }
      return new Blob([new Uint8Array(array)], { type: 'image/png' });
    },

    //  
    changeColorBridge() {
      this.color = "#5e5bc5";
      this.ctx.strokeStyle = this.color;
    },
    changeColorWooltari() {
      this.color = "#706419";
      this.ctx.strokeStyle = this.color;
    },
    changeColorHouse() {
      this.color = "#7f4502";
      this.ctx.strokeStyle = this.color;
    },
    changeColorPlatform() {
      this.color = "#8f2a91";
      this.ctx.strokeStyle = this.color;
    },
    changeColorRoof() {
      this.color = "#9600b1";
      this.ctx.strokeStyle = this.color;
    },
    changeColorBeukDol() {
      this.color = "#aad16a";
      this.ctx.strokeStyle = this.color;
    },
    changeColorDolBeuk() {
      this.color = "#ae2974";
      this.ctx.strokeStyle = this.color;
    },
    changeColorTreeWall() {
      this.color = "#b0c1c3";
      this.ctx.strokeStyle = this.color;
    },
    changeColorSoil() {
      this.color = "#6e6e28";
      this.ctx.strokeStyle = this.color;
    },
    changeColorMiniDol() {
      this.color = "#7c32c8";
      this.ctx.strokeStyle = this.color;
    },
    changeColorEct() {
      this.color = "#7d3054";
      this.ctx.strokeStyle = this.color;
    },
    changeColorMud() {
      this.color = "#87716f";
      this.ctx.strokeStyle = this.color;
    },
    changeColorGoodRoad() {
      this.color = "#8b3027";
      this.ctx.strokeStyle = this.color;
    },
    changeColorRoad() {
      this.color = "#946e28";
      this.ctx.strokeStyle = this.color;
    },
    changeColorBigSoil() {
      this.color = "#999900";
      this.ctx.strokeStyle = this.color;
    },
    changeColorCloud() {
      this.color = "#696969";
      this.ctx.strokeStyle = this.color;
    },
    changeColorFog() {
      this.color = "#77ba1d";
      this.ctx.strokeStyle = this.color;
    },
    changeColorHill() {
      this.color = "#7ec864";
      this.ctx.strokeStyle = this.color;
    },
    changeColorMountain() {
      this.color = "#869664";
      this.ctx.strokeStyle = this.color;
    },
    changeColorRiver() {
      this.color = "#9364c8";
      this.ctx.strokeStyle = this.color;
    },
    changeColorRock() {
      this.color = "#956432";
      this.ctx.strokeStyle = this.color;
    },
    changeColorSea() {
      this.color = "#9ac6da";
      this.ctx.strokeStyle = this.color;
    },
    changeColorSky() {
      this.color = "#9ceedd";
      this.ctx.strokeStyle = this.color;
    },
    changeColorSnow() {
      this.color = "#9e9eaa";
      this.ctx.strokeStyle = this.color;
    },
    changeColorDol() {
      this.color = "#a1a164";
      this.ctx.strokeStyle = this.color;
    },
    changeColorWater() {
      this.color = "#b1c8ff";
      this.ctx.strokeStyle = this.color;
    },
    changeColorBush() {
      this.color = "#606e32";
      this.ctx.strokeStyle = this.color;
    },
    changeColorFlower() {
      this.color = "#760000";
      this.ctx.strokeStyle = this.color;
    },
    changeColorPool() {
      this.color = "#7bc800";
      this.ctx.strokeStyle = this.color;
    },
    changeColorZip() {
      this.color = "#a2a3eb";
      this.ctx.strokeStyle = this.color;
    },
    changeColorTree() {
      this.color = "#a8c832";
      this.ctx.strokeStyle = this.color;
    },
    changeColorTreeColumn() {
      this.color = "#b57b00";
      this.ctx.strokeStyle = this.color;
    },

    // 
    onMouseMove(event) {
      // console.log(event);
      this.x = event.offsetX;
      this.y = event.offsetY;
      if (!this.painting) {
        this.ctx.beginPath();
        this.ctx.moveTo(this.x, this.y);
      } else {
        this.ctx.lineTo(this.x, this.y);
        this.ctx.stroke();
      }
    },
    onMouseDown(event) {
      console.log(event);
    },
    onMouseUp(event) {
      console.log(event);
      this.stopPainting();
    },
    stopPainting() {
      this.painting = false;
    },
    startPainting() {
      this.painting = true;
      this.tagItem = this.colorMap[this.color];
      this.appendTag();
    },

    // 
    eraseAll() {
      this.$router.go();
    }
  },
  mounted() {
    const canvas = document.getElementById("canvasId");
    let sizeWidth = 43 * window.innerWidth / 100;
    let sizeHeight = 80 * window.innerHeight / 100;
    canvas.width = sizeWidth;
    canvas.height = sizeHeight;
    canvas.style.width = sizeWidth;
    canvas.style.height = sizeHeight;
    this.ctx = canvas.getContext('2d');
    this.ctx.filter = "url(data:image/svg+xml;base64,#filter)";
    // this.ctx.strokeStyle = "#a8c832";
    this.ctx.strokeStyle = this.color;
    this.ctx.lineWidth = this.lineFat;
    this.ctx.imageSmoothingEnabled = false;
    this.ctx.mozImageSmoothingEnabled = false;
    this.ctx.oImageSmoothingEnabled = false;
    this.ctx.webkitImageSmoothingEnabled = false;
    this.ctx.msImageSmoothingEnabled = false;
    // ctx.fillStyle = 'green';
    // ctx.fillRect(10, 10, 150, 100);
    // this.ctx.filter = "none";
    if (canvas) {
      canvas.addEventListener("mousemove", this.onMouseMove);
      canvas.addEventListener("mousedown", this.startPainting);
      canvas.addEventListener("mouseup", this.stopPainting);
      canvas.addEventListener("mouseleave", this.stopPainting);
    }
  },
  watch: {
    lineFat() {
      // console.log(this.lineFat);
      this.ctx.lineWidth = this.lineFat;
    }
  }
}
</script>

